import React from 'react';
import { graphql } from 'gatsby';

const makeStyle = (edge) => ({color:edge.node.color})
export default ({data}) => {
  let listElements = data.allSampleJson.edges.map (edge => (<li style={makeStyle(edge)} >{edge.node.name}</li>))
  return <ul>{listElements}</ul>
}

export const pageQuery = graphql`
  query Foo {
    allSampleJson {
      edges {
        node {
          name,
          color
        }
      }
    }
  }
`